"use client";

import { useState, useEffect } from 'react';
import { useParams, useRouter } from 'next/navigation';
import Link from 'next/link';
import Image from 'next/image';
import Header from '@/components/ui/Header';
import Footer from '@/components/ui/Footer';
import { getProductById, Product } from '@/lib/products';

export default function ProductDetailPage() {
  const params = useParams();
  const router = useRouter();
  const productId = params.productId as string;
  
  const [product, setProduct] = useState<Product | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    async function fetchProduct() {
      if (!productId) return;
      
      try {
        setLoading(true);
        setError(null);
        const productData = await getProductById(productId);
        setProduct(productData);
      } catch (err) {
        setError(err instanceof Error ? err.message : 'Failed to load product');
      } finally {
        setLoading(false);
      }
    }

    fetchProduct();
  }, [productId]);

  if (loading) {
    return (
      <>
        <Header />
        <main className="flex-1 flex items-center justify-center py-20">
          <div className="text-center">
            <div className="inline-block animate-spin rounded-full h-8 w-8 border-b-2 border-gray-900"></div>
            <p className="mt-4 text-gray-600">Loading product details...</p>
          </div>
        </main>
        <Footer />
      </>
    );
  }

  if (error || !product) {
    return (
      <>
        <Header />
        <main className="flex-1 flex items-center justify-center py-20">
          <div className="text-center">
            <div className="text-red-500 text-6xl mb-4">❌</div>
            <h1 className="text-2xl font-bold text-gray-900 mb-2">Product Not Found</h1>
            <p className="text-gray-600 mb-6">
              {error || "The product you're looking for doesn't exist or has been removed."}
            </p>
            <div className="space-x-4">
              <button 
                onClick={() => router.back()} 
                className="bg-gray-200 text-gray-900 px-6 py-2 rounded-lg hover:bg-gray-300 transition-colors"
              >
                Go Back
              </button>
              <Link 
                href="/" 
                className="bg-gray-900 text-white px-6 py-2 rounded-lg hover:bg-gray-800 transition-colors inline-block"
              >
                Go Home
              </Link>
            </div>
          </div>
        </main>
        <Footer />
      </>
    );
  }

  return (
    <>
      <Header />
      
      <main className="flex-1">
        {/* Breadcrumb */}
        <section className="bg-gray-50 py-4">
          <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
            <nav className="flex items-center space-x-2 text-sm">
              <Link href="/" className="text-gray-600 hover:text-gray-900 transition-colors">
                Home
              </Link>
              <span className="text-gray-400">→</span>
              <Link 
                href={`/category/${product.category}`} 
                className="text-gray-600 hover:text-gray-900 transition-colors capitalize"
              >
                {product.category}
              </Link>
              <span className="text-gray-400">→</span>
              <span className="text-gray-900 font-medium truncate">{product.title}</span>
            </nav>
          </div>
        </section>

        {/* Product Details */}
        <section className="py-12 bg-white">
          <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
            <div className="grid grid-cols-1 lg:grid-cols-2 gap-12">
              {/* Product Image */}
              <div className="relative">
                <div className="aspect-square relative overflow-hidden rounded-lg bg-gray-100">
                  <Image
                    src={product.imageUrl}
                    alt={product.title}
                    fill
                    className="object-cover"
                    sizes="(max-width: 1024px) 100vw, 50vw"
                    priority
                  />
                </div>
              </div>

              {/* Product Info */}
              <div className="flex flex-col">
                <div className="mb-4">
                  <span className="inline-block bg-gray-100 text-gray-800 text-sm px-3 py-1 rounded-full capitalize mb-4">
                    {product.category}
                  </span>
                  <h1 className="text-3xl font-bold text-gray-900 mb-4">
                    {product.title}
                  </h1>
                  
                  {/* Rating */}
                  <div className="flex items-center mb-6">
                    <div className="flex items-center">
                      <span className="text-yellow-400 text-lg">★</span>
                      <span className="text-lg text-gray-700 ml-1 font-medium">
                        {product.rating}
                      </span>
                      <span className="text-gray-500 ml-2">
                        ({product.reviews} reviews)
                      </span>
                    </div>
                  </div>
                </div>

                {/* Price */}
                <div className="mb-8">
                  <div className="text-4xl font-bold text-gray-900 mb-2">
                    ₹{product.price}
                  </div>
                  <p className="text-gray-600">
                    Inclusive of all taxes • Free delivery available
                  </p>
                </div>

                {/* Description */}
                <div className="mb-8">
                  <h3 className="text-lg font-semibold text-gray-900 mb-3">Description</h3>
                  <p className="text-gray-700 leading-relaxed">
                    {product.description}
                  </p>
                </div>

                {/* Action Buttons */}
                <div className="space-y-4 mb-8">
                  <button className="w-full bg-gray-900 text-white py-4 px-6 rounded-lg font-semibold hover:bg-gray-800 transition-colors">
                    Add to Cart
                  </button>
                  <button className="w-full border-2 border-gray-900 text-gray-900 py-4 px-6 rounded-lg font-semibold hover:bg-gray-900 hover:text-white transition-colors">
                    Buy Now
                  </button>
                </div>

                {/* Product Features */}
                <div className="border-t pt-8">
                  <h3 className="text-lg font-semibold text-gray-900 mb-4">Product Features</h3>
                  <ul className="space-y-3 text-gray-700">
                    <li className="flex items-center">
                      <span className="text-green-500 mr-3">✓</span>
                      Fresh and high-quality ingredients
                    </li>
                    <li className="flex items-center">
                      <span className="text-green-500 mr-3">✓</span>
                      Same-day delivery available
                    </li>
                    <li className="flex items-center">
                      <span className="text-green-500 mr-3">✓</span>
                      Customization options available
                    </li>
                    <li className="flex items-center">
                      <span className="text-green-500 mr-3">✓</span>
                      100% satisfaction guarantee
                    </li>
                  </ul>
                </div>

                {/* Back Button */}
                <div className="mt-8 pt-8 border-t">
                  <button 
                    onClick={() => router.back()}
                    className="text-gray-600 hover:text-gray-900 transition-colors flex items-center"
                  >
                    <span className="mr-2">←</span>
                    Back to previous page
                  </button>
                </div>
              </div>
            </div>
          </div>
        </section>

        {/* Related Products Section */}
        <section className="py-16 bg-gray-50">
          <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
            <div className="text-center mb-12">
              <h2 className="text-2xl font-bold text-gray-900 mb-4">
                You might also like
              </h2>
              <p className="text-gray-600">
                Discover more products from the {product.category} category
              </p>
            </div>
            
            <div className="text-center">
              <Link 
                href={`/category/${product.category}`}
                className="bg-gray-900 text-white px-8 py-3 rounded-lg font-semibold hover:bg-gray-800 transition-colors inline-block"
              >
                View All {product.category.charAt(0).toUpperCase() + product.category.slice(1)}
              </Link>
            </div>
          </div>
        </section>
      </main>

      <Footer />
    </>
  );
}
